html{
  margin:0;
  width: 100%;
}
body {
  margin:0;
  width: 100%;
}
#root {
  padding:0;

}
.App {
  text-align: center;
  width:100%;
  padding:0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.NavBar {
  width: 100%;
  background:linear-gradient(to Left,  rgb(69,98,164), rgb(28,36,58)) ;
  height: 70px;
 
 
}

.search{
  width: 80%;
  height: 35px;
  margin-right:-30px;
  border:none;
  border-radius: 3px;
  padding-left:8px;
}

.toggleSwitch {
  
 
  padding:5px 0;
  margin-right: 0px;
 
 
  margin-left: 20px;
  
}
.newsLabel {
  font-size: 20px;
  color: white;
 
  margin-left: 20px;
  
}
.newsLabel2 {
  font-size: 20px;
  color: white;
  margin-right: 20px;
  margin-left: 20px;
  
}
main {

  width:100%;
  text-align: center;
  margin: auto;
  padding:20px 20px;
}

.newsCard {
  padding:25px 10px;
  border:solid rgb(238, 238, 238);
  border-width: thin;
  margin-bottom:40px;
  border-radius: 5px;
  box-shadow: 0 5px 10px 5px rgba(0, 0, 0, 0.253);
  
  
}
.imgContainer {
  width: 100%;

  padding:0;
  margin-left:0px;
}
.border{
  width: 100%;
  text-align: center;
  margin: auto;
  border: solid rgb(238, 238, 238);
  border-width: thin;
  padding:5px;
  object-fit:cover;
  border-radius: 5px;
}

.newsCard-img{ 
  max-width:100%;
  height:200px;
  background-repeat: no-repeat;
  background-size: cover;
 
}
.newsCard-title {
  font-size:22px;
  text-align: left;
  font-weight: bolder;
  font-style:italic;
}

.newsCard-description {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  font-size: 15px;
  text-align: left;
}

.newsCard-date{
  text-align: left;
  font-style: italic;
}
.newsCard-tag {
  text-align: right;
}
.modalTitle {
  font-size:18px;
}
.detail {
  margin:0;
  border:solid rgb(238, 238, 238);
  border-width: thin;
  margin-bottom:40px;
  border-radius: 5px;
  box-shadow: 0 5px 10px 5px rgba(0, 0, 0, 0.253);
}
.detail-title {
  text-align: left;
  margin-top: 20px;
  margin-bottom: 15px;
  font-size: 32px;
  font-style: italic;
  
  

}
.detail-date {
  font-style: italic;
  text-align: left;
}
.detail-img img{
  margin-top:10px;
  width:100%
}
.arrow {
  text-align: right;

}
.detail-description {
  text-align: left;
  font-size:15px;
}

.resultCard {
  padding:25px 10px;
  border:solid rgb(238, 238, 238);
  border-width: thin;
  border-radius: 5px;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.164);
  

  
}
.resultCard-title {
  font-size:15px;
  text-align: left;
  font-weight: bolder;
  font-style:italic;
  margin-bottom: 10px;
}
.resultCard-date{
  font-size:12px;
  text-align: left;
  font-style: italic;
  vertical-align: middle;
  
}
.resultCard-img{ 
  width:100%;
  height:200px;
  object-fit:cover;
  border: solid rgb(238, 238, 238);
  border-width: thin;
  padding:5px;
  margin:0;
  border-radius: 5px;
  margin-left:0px;
 
}
.sweet-loading{
  display: block;
}

.dropdown {
   
   width:100%;
   padding:0;
   margin: 0;
   background-color :rgba(0,0,0,0);
   border-color: solid rgb(238, 238, 238);
   border-width: thin;
   border-radius: 5px;
   height:38px
}
@media only  screen and (max-width:400px) {
  

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
